<template>
  <div class="data-view">
    <el-card>
      <div id="main1"></div>
    </el-card>
    <el-card>
      <div id="main2"></div>
    </el-card>
  </div>
</template>

<script>
import   dataview   from '@/assets/DataView.json'
export default {
  data(){return{}},
  mounted(){
    let myChart=this.$echarts.init(document.getElementById('main1'))
    myChart.setOption({
        title:{
          text:'大佬进阶班'
        },
      tooltip:{},
      xAxis:{
          data:['一班','二班','三班','四班','五班']
      },
      yAxis:{},
      //系列
      series:[{
          name:'人数',
          type:'line',
          data:[45,42,46,59,46,55,86,11]
      }]
    })
  },
  created() {
        this.draw()
  },
  methods:{
    draw(){
      let myChart1=this.$echarts.init(document.getElementById('main2'))
      let option={
        title:{
          text:'会话量',
        },
        tooltip:{
          trigger:'axis',
        },
        legend:{
          data:dataview.legend
        },
        xAxis:{
          type:'category',
          data:dataview.xAxis
        },
        yAxis:{
          typpe:'value',
        },
        series:dataview.series
      }
      myChart1.setOption(option)
    }
  }
}
</script>

<style scoped lang="scss">
.data-view{
  width: 100%;
  display: flex;
  justify-content: space-between;
  .el-card{
    width: 50%;
    #main1,#main2{
      height: 500px;
    }
  }
}
</style>